.section-default {
  border-top: 1px solid #ddd;
}
.section-default .section-container {
  text-align: center;
  padding: 48px 0;
}
.section-default .section-container .section-content {
  margin: 0 auto;
  max-width: 640px;
  padding: 24px 24px 0;
}
.gfx-wrapper {
  width: 90vw;
  height: 90vw;
  margin: 0 auto;
  position: relative;
  overflow: hidden;
}
.gfx-wrapper.height-forty {
  height: 36vw;
}
.gfx-wrapper.height-seventy {
  height: 62.99999999999999vw;
}
@media screen and (min-width: 800px) {
  .gfx-wrapper {
    -webkit-transform: scale(0.8);
    transform: scale(0.8);
    overflow: visible;
  }
  .gfx-wrapper.square {
    margin: -4.5vw auto;
  }
  .gfx-wrapper.height-forty {
    height: 28.8vw;
  }
  .gfx-wrapper.height-seventy {
    height: 50.4vw;
  }
}
@media screen and (min-width: 960px) {
  .gfx-wrapper {
    -webkit-transform: scale(0.65);
    transform: scale(0.65);
  }
  .gfx-wrapper.square {
    margin: -14.625vw auto;
  }
  .gfx-wrapper.height-forty {
    height: 23.400000000000002vw;
  }
  .gfx-wrapper.height-seventy {
    height: 40.949999999999996vw;
  }
}
@media screen and (min-width: 1200px) {
  .gfx-wrapper {
    -webkit-transform: scale(0.5);
    transform: scale(0.5);
  }
  .gfx-wrapper.square {
    margin: -22.5vw auto;
  }
  .gfx-wrapper.height-forty {
    height: 18vw;
  }
  .gfx-wrapper.height-seventy {
    height: 31.499999999999996vw;
  }
}
.two-hubs #two-hubs-one .hub {
