  margin-top: -0.36vw;
}
.three-hubs #three-hubs-one .hub {
  left: 13%;
}
.three-hubs #three-hubs-two .hub {
  left: 50%;
}
.three-hubs #three-hubs-three .hub {
  left: 87%;
}
.three-hubs .data-bundle {
  top: 50%;
  left: 10%;
}


